<template>
    <div class="bill-details">
        <div class="top-card">
            <p class="top">应还款金额(元)</p>
            <p class="money">￥ <span class="num">{{data.repayAmount | numToMoney}}</span></p>
            <div class="date flex flex-between">
                <div class="inline-block">
                    应还款日 {{data.lendEndDate}}
                </div>
                <div class="state inline-block text-right" :class="{had: data.repayStatus === 40, overdue: data.repayStatus === 10}">
                    {{data.repayMsg}}
                </div>
            </div>
        </div>
        <ul class="form">
            <li class="flex flex-between">
                <div class="title">借款金额(元)</div>
                <div class="content">{{data.lendRealAmount | numToMoney}}</div>
            </li>
            <li class="flex flex-between">
                <div class="title">服务费和利息(元)</div>
                <div class="content">{{data.serviceAmount | numToMoney}}</div>
            </li>
            <li class="flex flex-between">
                <div class="title">借款日期</div>
                <div class="content">{{data.lendStartDate}}</div>
            </li>
            <li class="flex flex-between">
                <div class="title">逾期费用(元)</div>
                <div class="content">{{data.overdueAmount | numToMoney}}</div>
            </li>
            <li class="flex flex-between">
                <div class="title">逾期天数</div>
                <div class="content">{{data.repayDays > 0 ? 0 : -data.repayDays}}</div>
            </li>
            <li class="flex flex-between">
                <div class="title">借款合同</div>
                <div class="content"><router-link :to="agreementHref">查看借款合同 ></router-link></div>
            </li>
        </ul>
        <cube-button v-if="data.repayStatus != 10" class="btn-main re-btn" @click="refund" :primary="true">立即还款</cube-button>
    </div>
</template>

<script>
import { fetchHeader } from '../api/axios.js'
export default {
    data() {
        return {
            id: '',
            data: {
                repayAmount: '',
                lendRealAmount: '',
                serviceAmount: '',
                lendStartDate: '',
                lendEndDate: '',
                overdueAmount: '',
                repayDays: '',
                repayMsg: '',
                repayStatus: '' // 40 已还清
            },
            agreementHref: ''
        }
    },
    methods: {
        refund() {
            this.$router.push('payDetails?id=' + this.id)
        },
        async getData() {
            const data = await fetchHeader('/bill/billDetail', { id: this.id }, 'post')
            console.log(data)
            if (data.code === 0) {
                Object.keys(this.data).map(item => {
                    this.data[item] = data.data[item]
                })
            }
            this.agreementHref = data.data.agreement.href
        }
    },
    mounted() {
        document.title = '账单详情'
        this.id = this.$route.query.id
        this.getData()
    }
}
</script>

